.center--Container{
  cursor: pointer;
}

.center--Container .hoverDiv.orange_gray {
  background-color: #ff7817;
  opacity: 0.95;
}

.center--Container .text2D.orange_gray {
  color: var(--theme-color-fg-primary);
  font: 600 15px/26px "Google Sans", Roboto, Arial, sans-serif;
  letter-spacing: 1.1px;
  z-index: 25;
}








.blocks__sort ._filter__Wrapper .__icon_filter svg  {
