.__View__MakingBet_{
  background-color: hsl(0,0%,17%);  
  overflow: hidden;
}


.__View__MakingBet_ ._container_{ 
  height: calc(100vh - 55px);
  overflow-y: scroll;
}


.__View__MakingBet_ ._container_ ._header_Cock_{
  margin-top:50px;
}

.__View__MakingBet_ ._container_.noneMrgTop ._header_Cock_{
  margin-top:0px;
}






.__View__MakingBet_ ._container_ ._balance_wrapper_ ._balance_{
  display: flex;
  padding: 15px 15px 6px;
}

.__View__MakingBet_ ._container_ ._balance_wrapper_ ._balance_ ._name_{
  color: var(--fire-color-canvas);
  font: 400 13px/19px "Roboto","Arial","Open Sans",sans-serif;
  margin-left:15px; 
}

.__View__MakingBet_ ._container_ ._balance_wrapper_ ._balance_ ._name_._notEnoughtBalance_{
  color: #f50000;  
}

.__View__MakingBet_ ._container_ ._balance_wrapper_  ._action_container_{
   padding :  0; 
}

.__View__MakingBet_ ._container_ ._balance_wrapper_ ._action_container_ .__add_balance_btn__ {
  margin :  0px auto; 
  color: var(--fire-color-canvas);
  font: 400 15px/19px "Roboto","Arial","Open Sans",sans-serif;
  border-radius: 32px;
  padding: 9px 0;
  border: 1px solid var(--fire-color-canvas);
  max-width: 160px;

}


















.__View__MakingBet_ ._container_ ._make_bet_{  
  padding: 0  0 55px;
}



.__View__MakingBet_ ._container_ ._make_bet_ ._details_{
  display: flex;
  padding: 0 15px;
  margin-top: 15px; 
  min-height: 48px;
}

.__View__MakingBet_ ._container_ ._make_bet_ ._details_ .__icon_ svg{
  fill: var(--fire-color-canvas);  
  margin-right: 15px; 
}

.__View__MakingBet_ ._container_ ._make_bet_ ._details_ ._description{
  color: var(--fire-color-canvas);   
  font: 400 13px/17px "Roboto","Arial","Open Sans",sans-serif;
}

.__View__MakingBet_ ._container_ ._make_bet_ ._details_ ._ammount{
  color: var(--fire-color-canvas);
  font: 400 15px/19px "Roboto","Arial","Open Sans",sans-serif;
  width: 270px;
}


.__View__MakingBet_ ._container_ ._make_bet_ ._details_ .__icon_ svg{
  margin: 10px 15px 0 0;
}

.__View__MakingBet_ ._container_ ._make_bet_ ._details_ ._description {
  margin: 12px 0 0;
}

.__View__MakingBet_ ._container_ ._make_bet_ ._title_{
  color: var(--fire-color-canvas);
  font: 500 18px/21px "Roboto","Arial","Open Sans",sans-serif;
}

.__View__MakingBet_ ._container_ ._make_bet_ .actions{
  padding: 40px 0; 
}






