body {    
    min-height: 400px;
    font-size: 100%;
    background-color: var(--background--color);
    color: var(--colorText_);
    font-family: Roboto,Helvetica,Arial,sans-serif;
    line-height: 1.6;
    margin: 0;
    padding: 0;  
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color:   #f9f9f9;
    background:  var(--theme-color-hrm-bkgr-primary);
}


.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.content_body{
  max-width: 1260px;    
}


.content_home{
  margin-top: 50px;
}

.content_details{
  margin-top: 65px;
}

.content_forms{
  background: var(--background--color--cards);
  padding: 25px 0;
}



.flexSpace {
  -ms-flex: 1 1 0.000000001px;
  flex: 1 1;
  -ms-flex-preferred-size: 0.000000001px;
  flex-basis: 0.000000001px;
}
.FlexList {
  box-align: start;
  -ms-flex-align: start;
  align-items: flex-start;
  display: flex;
  flex-wrap: wrap;
}

.FlexItem {
  box-flex: 1;
  -ms-flex-positive: 1;
  flex-grow: 1;
  display: inline-block;
  text-align: center;
}


.loadingScreen{
  height: 100%;
  width: 100%;
  position: fixed; /* Stay in place */
  z-index: 210; /* Sit on top */
  left: 0;
  top: 0;
  background-color: var(--color-base--hover);
  /*background-color: rgba(0,0,0, 0.6); /* Black w/opacity */
  overflow-x: hidden; /* Disable horizontal scroll */
  transition: 0.25s; /* 0.5 second transition effect to slide in or slide down the overlay (height or width, depending on reveal) */  
  padding: 70% 0; 
}




