.spinnerSvg {
    -webkit-animation: rotate 2s linear infinite;
            animation: rotate 2s linear infinite;
    height: 100%;
    -webkit-transform-origin: center center;
            transform-origin: center center;
    width: 100%;
    position: absolute;
    top: 0; bottom: 0; left: 0; right: 0;
    margin: auto;
  }
  
  .spinnerPath {
    /* Fall back for IE 11, where the stroke properties are not animated,
       but the spinner still rotates. */
    stroke: #d62d20;
    stroke-dasharray: 20, 200;
    stroke-dashoffset: 0;
  
    -webkit-animation:
      dash 1.5s ease-in-out infinite,
      color 6s ease-in-out infinite;
  
            animation:
      dash 1.5s ease-in-out infinite,
      color 6s ease-in-out infinite;
    stroke-linecap: round;
  }
  .spinnerPathWC {
    /* Fall back for IE 11, where the stroke properties are not animated,
       but the spinner still rotates. */
    stroke: #d62d20;
    stroke-dasharray: 20, 200;
    stroke-dashoffset: 0;  
    -webkit-animation: dash 1.5s ease-in-out infinite;  
            animation: dash 1.5s ease-in-out infinite;
    stroke-linecap: round;
  }
  
  @-webkit-keyframes rotate {
    100% {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
  
  @keyframes rotate {
    100% {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
  
  @-webkit-keyframes dash {
    0% {
      stroke-dasharray: 1, 200;
      stroke-dashoffset: 0;
    }
    50% {
      stroke-dasharray: 89, 200;
      stroke-dashoffset: -35px;
    }
    100% {
      stroke-dasharray: 89, 200;
      stroke-dashoffset: -124px;
    }
  }
  
  @keyframes dash {
    0% {
      stroke-dasharray: 1, 200;
      stroke-dashoffset: 0;
    }
    50% {
      stroke-dasharray: 89, 200;
      stroke-dashoffset: -35px;
    }
    100% {
      stroke-dasharray: 89, 200;
      stroke-dashoffset: -124px;
    }
  }
  
  @-webkit-keyframes color {
    100%, 0% {
      stroke: #d62d20;
    }
    40% {
      stroke: #0057e7;
    }
    66% {
      stroke: #008744;
    }
    80%, 90% {
      stroke: #ffa700;
    }
  }
  
  @keyframes color {
    100%, 0% {
      stroke: #d62d20;
    }
    40% {
      stroke: #0057e7;
    }
    66% {
      stroke: #008744;
    }
    80%, 90% {
      stroke: #ffa700;
    }
  }
  
  
  #loadingSpinner {
    margin: auto;
    width: 100px;
    height: 100px;
  
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
  }
  

  .Icon_Cnt{
    position:relative;
  }



  .SpriteSvg {
    border: 2px solid firebrick;
    background: rgba(213, 0, 0,0.52);    
    margin: -7px -7px 0;
    position: absolute;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    -webkit-animation: IconSpriteAll 2.2s linear infinite;
            animation: IconSpriteAll 2.2s linear infinite;

  }
  .SpritePath {
    -webkit-animation: IconSprite 3s linear infinite;
            animation: IconSprite 3s linear infinite;    
  }



  @-webkit-keyframes IconSprite22 {
    0%{background-position:0px 0px}
    25%{background-position:0px 0px}
    100%{background-position:0px -2000px}
  }



  @keyframes IconSprite22 {
    0%{background-position:0px 0px}
    25%{background-position:0px 0px}
    100%{background-position:0px -2000px}
  }

  .loader:before {
    z-index: 2;
    -webkit-animation: IconSprite 2s infinite;
    animation: IconSprite 2s infinite;
  }




  @-webkit-keyframes IconSprite {
    0% {
      -webkit-transform: scale(0.8);
      opacity: 0.5;
    }
    50% {
      opacity: 1;
      -webkit-transform: scale(1.1);
    }
    100% {
      -webkit-transform: scale(0.8);
      opacity: 0.5;
    }
  }
  @keyframes IconSprite {
    0% {
      -webkit-transform: scale(0.8);
              transform: scale(0.8);
      opacity: 0.5;
    }
    50% {
      -webkit-transform: scale(1.1);
              transform: scale(1.1);
      opacity: 1;
    }
    100% {
      -webkit-transform: scale(0.8);
              transform: scale(0.8);
      opacity: 0.5;
    }
  }




  @-webkit-keyframes IconSpriteAll {
    0% {
      -webkit-transform: scale(0.2);
      opacity: 0.1;
    }
    50% {
      opacity: 1;
    }
    100% {
      -webkit-transform: scale(1.2);
      opacity: 0;
    }
  }
  @keyframes IconSprite {
    0% {
      -webkit-transform: scale(0.2);
              transform: scale(0.2);
      opacity: 0.1;
    }
    50% {
      opacity: 1;
    }
    100% {
      -webkit-transform: scale(1.2);
              transform: scale(1.2);
      opacity: 0;
    }
  }
.SlideMenu{
    display: block;    
    box-shadow: 0 15px 12px rgba(0,0,0,0.22), 0 0 38px rgba(0,0,0,0.30);    
    position: fixed;    
    top:0;
    height: 100%;;
    z-index: 220;
    border-radius: 3px;
    opacity: 1;
    min-width: 240px;
    /*transition:transform 250ms cubic-bezier(0.8, 0, 0.6, 1) 0ms;*/    
    transition-property: -webkit-transform;
    -webkit-transition-property: -webkit-transform;
    transition-property: transform;
    transition-property: transform, -webkit-transform;
    -webkit-transform: translate3d(-110%, 0, 0);    
    transform: translate3d(-110%, 0, 0);
    -webkit-transition-duration: 200ms;
            transition-duration: 200ms;
    background-color:var(--background--color);
    left:0;
}

.SlideMenu.show{
    -webkit-transform: translate3d(0, 0, 0);    
    transform: translate3d(0, 0, 0);
}


.SlideMenuOverlay{
    height: 100%;
    width: 100%;
    position: fixed;
    z-index: 210; 
    left: 0;
    top: 0;
    background-color: rgba(0,0,0,0.22); 
    overflow-x: hidden;   
}


