/*
  carousel Style;
*/











[carousel] {
  position: relative;
}


[carousel] .carousel-wrapper2 {
  margin-left: 48px;
  max-width: 250px;
  min-width: 250px;
}



.carousel-wrapper {
  position: relative;
  width: 100%;
  z-index: 5;
}



.carousel-element {
  height: inherit;
  position: relative;
  width: 2000%;
  -webkit-transition: -webkit-transform 400ms cubic-bezier(0.25,0.46,0.45,0.94);
  transition: -webkit-transform 400ms cubic-bezier(0.25,0.46,0.45,0.94);
  transition: transform 400ms cubic-bezier(0.25,0.46,0.45,0.94);
  transition: transform 400ms cubic-bezier(0.25,0.46,0.45,0.94), -webkit-transform 400ms cubic-bezier(0.25,0.46,0.45,0.94);
}

.carousel-element::before, .carousel-element::after {
  content: "";
  display: table;
}

.grab {
  /*cursor: url(../img/openhand.cur) 8 8,move;*/
  cursor: -webkit-grab;
  cursor: grab;
}


[carousel] .carousel-slide {
  margin: 0;
}

.carousel-slide {
  float: left;
  height: 100%;
  overflow: hidden;
  width: 5%;
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
}



@media only screen and (max-width: 500px){    
  .carousel-slide {  
    width: 50%;  
  }

}












